.progress {
  padding: 20px;
}

.progress h2 {
  padding: 10px;
  color: white;
  background-color: black;
  border-radius: 30px 30px 0px 30px;
}

.attempts {
  margin-top: 20px;
}

.attempt {
  background-color: white;
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.attempt p {
  margin: 5px 0px;
}
